.wrapper {
    width: 420;
    background: #004B84;
    color:aliceblue;
    border-radius: 10px;
    padding: 30px 40px;
    margin: 200px 350px;
}

.wrapper h1 {
    font-size: 36px;
    text-align: center;
}

.wrapper .input-box {
    position: relative;
    width: 90%;
    height: 30%;
    margin: 30px 0px;
}
.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, .2);
    border-radius:40px;
    font-size: 16px;
    color: #fff;
    padding: 20px 45px 20px 20px;
}

.input-box input::placeholder {
    color: #fff; 
}

.input-box .icon {
    position: absolute;
    right:20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}

.input-box .remember-forgot{
    display: flex;
    justify-content: space-between;
    font-size: 14.5;
    margin: -15px 0 15px;
}
.remember-forgot label input{
    accent-color: #fff;
    margin-right: 4px;
}
.remember-forgot a {
    color: #fff;
    text-decoration: none;
}

.remember-forgot a:hover{
    text-decoration: underline;
}

.wrapper button {
    width: 100%;
    height: 45px;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
}
.wrapper button:hover {
    width: 100%;
    height: 45px;
    background: #FF99BF;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
}
.wrapper .register-link {
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}
.remember-forgot{
    margin: 20px 0px;
}
.register-link p a {
    color: #fff;
    text-decoration: none;
    font-weight: 600; 
}

.register-link p a:hover {
    text-decoration: underline;
}

